@charset "UTF-8";
/* reset.cssここから */
*,
::before,
::after {
  box-sizing: border-box;
}

body {
  margin: 0;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

p,
iframe,
figure {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  line-height: inherit;
  font-weight: inherit;
  margin: 0;
}

ul,
ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

hr {
  height: 0;
  overflow: visible;
  margin: 0;
  padding: 0;
  border: 0;
  overflow: visible;
}

a {
  background-color: transparent;
  text-decoration: none;
  color: inherit;
}

b,
strong {
  font-weight: bolder;
}

img,
embed,
object,
iframe {
  vertical-align: bottom;
}

.contents-layout {
  display: flex;
  flex-direction: column;
  row-gap: 100px;
  padding-bottom: 100px;
}

.ly-w {
  max-width: 1120px;
  padding-left: 40px;
  padding-right: 40px;
  margin: 0 auto;
}
@media (max-width: 850px) {
  .ly-w {
    padding-left: 3.75vw;
    padding-right: 3.75vw;
  }
}

#hamburger,
.hamburger__bar {
  display: inline-block;
  transition: all 0.2s;
  box-sizing: border-box;
  display: none;
}
@media (max-width: 850px) {
  #hamburger,
.hamburger__bar {
    display: block;
  }
}

#hamburger {
  position: absolute;
  width: 12.5vw;
  height: 12.5vw;
  background: none;
  border: none;
  appearance: none;
  right: 5vw;
  cursor: pointer;
  z-index: 30;
}

.hamburger__bar {
  position: absolute;
  left: 0;
  width: 100%;
  height: 0.46875vw;
  background-color: red;
  border-radius: 4px;
}

.barTop {
  top: 25%;
}

.barMiddle {
  top: 45%;
}

.barBottom {
  top: 65%;
}

#hamburger::after {
  position: absolute;
  left: 0;
  bottom: -1vw;
  content: "MENU";
  display: block;
  width: 100%;
  color: red;
  font-size: 3vw;
  text-decoration: none;
  text-align: center;
  white-space: nowrap;
  transition: all 0.2s;
}

#hamburger.active::after {
  content: "CLOSE";
  bottom: -1vw;
}

#hamburger.active .barTop {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(-45deg);
}

#hamburger.active .barMiddle {
  opacity: 0;
}

#hamburger.active .barBottom {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
}

.no-scroll {
  overflow: hidden;
}

#header {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 20;
  height: 80px;
}
@media (max-width: 850px) {
  #header {
    position: fixed;
    height: 12.5vw;
  }
}

@media (max-width: 850px) {
  .siteLogo {
    width: 15.625vw;
  }
}

.siteLogo__img {
  width: 100%;
  height: 100%;
}

@media (max-width: 850px) {
  .globalNav {
    display: none;
  }
}
.globalNav .menu {
  display: flex;
  column-gap: 32px;
  align-items: center;
  justify-content: flex-start;
}
.globalNav .menu__item {
  font-size: 1rem;
}
.globalNav .menu__item a {
  position: relative;
  display: inline-block;
}
.globalNav .menu__item a::after {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  content: "";
  transition: transform 0.3s;
  transform: scale(0, 1);
  transform-origin: left top;
  background: #333;
}
.globalNav .menu__item a:hover::after {
  transform: scale(1, 1);
}
.globalNav .menu__item:last-child {
  margin-right: 0;
}

.globalNav--sp {
  position: fixed;
  z-index: 20;
  top: 0;
  right: -120%;
  width: 100vw;
  height: 100vh;
  opacity: 0;
  background-color: #56e713;
  transition: 0.2s;
  display: grid;
  place-items: center;
}
.globalNav--sp .menu {
  font-size: 7vw;
  line-height: 20vw;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  letter-spacing: 1.5vw;
}
.globalNav--sp .menu__item a {
  color: #ebebeb;
}

.js-slideIn {
  right: 0;
}

.loading-simple-bg {
  background-color: #7b0199;
}

#loading-simple {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #000;
  z-index: 99999;
}

.loading-text {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  text-align: center;
  width: 100%;
  height: 100px;
  line-height: 100px;
}
.loading-text span {
  display: inline-block;
  margin: 0 5px;
  color: #fff;
}
.loading-text span:nth-child(1) {
  filter: blur(0px);
  animation: blur-text 1.5s 0s infinite linear alternate;
}
.loading-text span:nth-child(2) {
  filter: blur(0px);
  animation: blur-text 1.5s 0.2s infinite linear alternate;
}
.loading-text span:nth-child(3) {
  filter: blur(0px);
  animation: blur-text 1.5s 0.4s infinite linear alternate;
}
.loading-text span:nth-child(4) {
  filter: blur(0px);
  animation: blur-text 1.5s 0.6s infinite linear alternate;
}
.loading-text span:nth-child(5) {
  filter: blur(0px);
  animation: blur-text 1.5s 0.8s infinite linear alternate;
}
.loading-text span:nth-child(6) {
  filter: blur(0px);
  animation: blur-text 1.5s 1s infinite linear alternate;
}
.loading-text span:nth-child(7) {
  filter: blur(0px);
  animation: blur-text 1.5s 1.2s infinite linear alternate;
}

@keyframes blur-text {
  0% {
    filter: blur(0px);
  }
  100% {
    filter: blur(4px);
  }
}
.mainVisual {
  grid-area: main;
  height: 500px;
}
@media (max-width: 850px) {
  .mainVisual {
    height: 100vh;
  }
}

.mainVisual__inner {
  max-width: 100%;
  height: 100%;
  position: relative;
  display: grid;
  place-items: center;
}
.mainVisual__inner::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: #000;
  opacity: 0.5;
}

.mainVisual__img--pc {
  display: block;
  width: 100%;
  height: 100%;
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
}
@media (max-width: 850px) {
  .mainVisual__img--pc {
    display: none;
  }
}

.mainVisual__img--sp {
  display: none;
  width: 100%;
  height: 100vh;
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
}
@media (max-width: 850px) {
  .mainVisual__img--sp {
    display: block;
  }
}

.mainVisual__txtWrap {
  position: absolute;
}

body::after {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  z-index: 9999;
  pointer-events: none;
  opacity: 0;
  transition: opacity 0.4s ease;
}

body.js-fadeout::after {
  opacity: 1;
}